.reviews-section {

  // .reviews-section__container

  &__container {}
}

.main {

  // .main__reviews-section

  &__reviews-section {}
}

.reviews {

  .swiper {
    width: 100%;
    height: 100%;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .reviews__title

  &__title {
    margin-bottom: 120px;
  }

  // .reviews__content

  &__content {}

  // .reviews__footer

  &__footer {
    a {
        margin: 5px 0 !important;
      }
  }
}


.reviews-content {

  .swiper-slide {
    opacity: .4;
    width: 100%;
    height: 100%;
  }

  .swiper-slide-active {
    opacity: 1;
  }

  // .reviews-content__item

  &__item {
    background: var(--grey-color);
    position: relative;
  }

  // .reviews-content__img

  &__img {
    img {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      position: absolute;
      top: -60px;
      left: 50%;
      transform: translateX(-50%);
    }

    // overflow: hidden;
  }

  // .reviews-content__text

  &__text {
    margin: 130px auto 0;
    width: 70%;
    position: relative;
    text-align: center;

    &::before {
      content: '';
      position: absolute;
      display: block;
      background: url('../img/svg/quote.svg') 0 0 / contain no-repeat;
      width: 40px;
      height: 40px;
      top: -60px;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  // .reviews-content__truncate-text

  &__truncate-text {
    background: var(--grey-color);
    width: 100%;
    height: 200px;
    overflow: hidden;
    position: relative;
    color: var(--grey-dark-color);
    font-size: 16px;
    font-style: italic;

    p {
      font-size: 16px;
      font-style: italic;
    }


    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 40px;
      background: linear-gradient(180deg, transparent, var(--grey-color) 50%);
    }
  }

  // .reviews-content__btns

  &__btns {
    text-align: center;
    margin-bottom: 40px;
  }
}
