.footer {
  background-color: var(--blue-dark-color);
  padding: 50px 0;
  color: var(--light-color);
  font-size: 14px;
  line-height: 140%;

  a,
  p,
  ul {
    color: var(--light-color);
    font-size: 14px;
    line-height: 160%;
  }

  a {
    transition: color .3s ease-in-out;

    @include hover {
      color: var(--accent-color);
    }
  }

  h2 {
    color: var(--accent-color);
    font-size: 18px;
    letter-spacing: 1.1px;
    margin-bottom: 30px;
  }

  li {
    margin-bottom: 14px;
  }

  // .footer__items

  &__items {}

  // .footer__item

  &__item {}

  // .footer__image

  &__logo {
    margin-bottom: 20px;
  }

  // .footer__contacts

  &__contacts {
    display: flex;
    align-items: center;
    gap: 10px;
    &::before{
      content: '';
      display: inline-block;
      width: 20px;
      height: 20px;
    }
  }

  // .footer__contacts--address

  &__contacts--address {
    &::before{
      background:url('../img/svg/address.svg') 0 0 / contain no-repeat;
    }
  }

  // .footer__contacts--phone

  &__contacts--phone {
    &::before {
        background: url('../img/svg/phone.svg') 0 0 / contain no-repeat;
      }
  }

  // .footer__contacts--mail

  &__contacts--mail {
    &::before {
        background: url('../img/svg/mail.svg') 0 0 / contain no-repeat;
      }
  }

  // .footer__contacts--fb

  &__contacts--fb {
    &::before {
        background: url('../img/svg/fb.svg') 0 0 / contain no-repeat;
      }
  }
&__contacts--vk {
  &::before {
    background: url('../img/svg/vk.svg') 0 0 / contain no-repeat;
  }
}
  // .footer__contacts--inst

  &__contacts--inst {
    &::before {
        background: url('../img/svg/inst.svg') 0 0 / contain no-repeat;
      }
  }

  // .footer__contacts--wts

  &__contacts--wts {
    &::before {
        background: url('../img/svg/wts.svg') 0 0 / contain no-repeat;
      }
  }

  // .footer__contacts--yandex

  &__contacts--yandex {
    &::before {
        background: url('../img/svg/ynd.svg') 0 0 / contain no-repeat;
      }
  }
}

.main {

  // .main__footer

  &__footer {}
}
