.tooltip {
  color: var(--second-color);
  text-decoration: underline;
  cursor: pointer;
  cursor: default;
  transition: color .2s ease-in-out;

  @include hover {
    color: var(--accent-color);
    text-decoration: none;
  }
}

[data-tooltip] {
  position: relative;
  display: inline-block;

  &::before,
  &::after {
    position: absolute;
    top: 50%;
    background-color: var(--accent-color);
    z-index: -1;
    opacity: 0;
    visibility: hidden;
    transition: all .1s ease-in-out;
  }

  &::before {
    content: attr(data-tooltip);
    left: calc(100% + 14px);
    top: 50%;
    transform: translateY(-50%);
    color: var(--black-color) !important;
    border-radius: 10px;
    padding: 10px 20px;
    min-width: 144px;
    font-size: 14px;
    font-weight: 400;
    line-height: 120%;
    text-align: left;
  }

  &::after {
    content: '';
    width: 20px;
    height: 20px;
    left: 100%;
    transform: rotate(45deg) translateY(-50%);
  }

  @include hover {

    &::before,
    &::after {
      opacity: 1;
      visibility: visible;
    }

    &::before {
      z-index: 3;
      left: calc(100% + 18px);
    }

    &::after {
      z-index: 2;
      left: calc(100% + 4px);
    }
  }
}
