.accordion {
  width: 100%;
  background-color: #f8f7f5;
}

.accordion__btn,
.accordion__content {
  margin: 0;
  border: 0;
}

.accordion__btn {
  color: var(--blue-color);
  font-size: 20px;
  line-height: 130%;
  font-weight: 500;
  padding: 20px 20px 16px 60px;
  position: relative;
  cursor: pointer;
  transition: color .3s ease-in-out;

  &.open {
    color: var(--accent-color);
  }

  @include hover {
    color: var(--accent-color);
  }
}

.accordion__btn::before {
  content: "+";
  position: absolute;
  width: 60px;
  left: 0;
  top: 50%;
  bottom: 0;
  color: var(--accent-color);
  content: "+";
  font-family: inherit;
  font-size: 24px;
  font-weight: 600;
  text-align: center;
  transform: translateY(-50%);
  transition: opacity 0.3s ease-in-out;
}

.accordion__content {
  display: grid;
  grid-template-rows: 0fr;
  overflow: hidden;
  transition: grid-template-rows .2s ease-in-out;
}

.accordion__content div {
  padding-left: 60px;
  padding-right: 60px;
  word-wrap: break-word;
  color: #8a8a9a;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;

  p {
    font-size: 16px;
  }

  p:last-child {
    margin-bottom: 30px;
  }

  @include mobile {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.accordion__content.open {
  grid-template-rows: 1fr;
}

.accordion__btn.open::before {
  content: "-";
  font-size: 40px;
  font-weight: 100;
}


