.title {
  color: var(--blue-color);
  line-height: 140%;

  span {
    color: var(--accent-color);
  }

  @include mobile {
    br {
      display: none;
    }
  }

  &--h1 {
    @include adaptiv-font(48, 34);
  }

  &--h2 {
    @include adaptiv-font(42, 30);
    line-height: 120%;
  }

  &--h3 {
    @include adaptiv-font(34, 26);
    line-height: 120%;
  }

  &--w {
    color: var(--light-color);
  }

  &--y {
    color: var(--accent-color);
  }

  &--line {
    padding-bottom: 10px;
    position: relative;
    margin-bottom: 60px;

    &::before,
    &::after {
      content: '';
      position: absolute;
      left: 0;
    }

    &::before {
      bottom: -10px;
      height: 1px;
      width: 100%;
      max-width: 350px;
      background-color: var(--grey-light-color);
    }

    &::after {
      bottom: -13px;
      height: 8px;
      width: 100px;
      background-color: var(--accent-color);
    }
  }

  &--ff {
    font-family: var(--font-family);
    font-weight: 600;
  }
}


.modal-title {
  color: var(--blue-color);
  line-height: 140%;
  @include adaptiv-font (34, 24);
  font-family: var(--font-family);
  font-weight: 700;

  &--h3 {
    color: var(--grey-dark-color);
    font-size: 24px;
    font-family: var(--font-family);
  }
}
