.schedule {

  // .schedule__container

  &__container {}

  // .schedule__content

  &__content {
    display: flex;
    gap: 50px;

    @include tablet-1120 {
      flex-direction: column;
    }
  }

  // .schedule__title

  &__title {
    flex: 0 1 30%;
    max-width: 30%;

    @include tablet-1120 {
      flex: 100%;
      max-width: 100%;
    }
  }

  // .schedule__text

  &__text {

    p {
      font-size: 17px !important;
    }

  }

  // .schedule__desc

  &__desc {
    padding: 30px;
    background-color: var(--accent-color-02);
    border-radius: 24px;
    margin-bottom: 40px;

    &--record {
      background-color: transparent;
    }
  }

  // .schedule__items

  &__items {
    margin-top: 50px;
    justify-content: space-between;

    @include tablet-830 {
      flex-wrap: wrap;
    }
  }

  // .schedule__item

  &__item {
    border-radius: 24px;
    padding: 20px;
    font-weight: 700;
    padding-left: 60px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 16px;

    span {
      color: var(--second-color);
    }

    button {
      align-self: center;
      margin: 20px 0 0;
    }
  }

  // .schedule__item--moon

  &__item--moon {
    background: var(--grey-color) url('../img/svg/moon.svg') 10px 10px / 60px no-repeat;
  }

  // .schedule__item--sun

  &__item--sun {
    background: var(--grey-color) url('../img/svg/sun.svg') 10px 10px / 60px no-repeat;
  }

  // .schedule__item--online

  &__item--online {
    background: var(--grey-color) url('../img/svg/online.svg') 10px 10px / 60px no-repeat;
  }
}
