.tariffs {
  // .tariffs__content

  &__content {
    padding: 0;
  }

  // .tariffs__section
  &__section {
    color: var(--content-color);
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 30px;

    @include tablet-960 {
      flex-wrap: nowrap;
      justify-content: start;
      overflow-x: auto;
      padding: 30px;
      margin: 0 -30px;
      align-items: flex-start;
    }

  }

  // .tariffs__item

  &__item {
    h2 {
      font-family: var(--font-family);
      @include adaptiv-font(20, 18);
      line-height: 140%;
      color: var(--heading-color);
      font-weight: 500;
      margin: 30px 0 60px;
      // max-width: 760px;
      // text-align: center;
    }
  }
}

.tariff-card {
  background-color: var(--grey-color-bg);
  border-radius: 8px;
  transition-duration: 0.3s;
  padding: 40px;
  flex: 0 1 calc(50% - 15px);
  max-width: calc(50% - 15px);

  @include tablet-960 {
    padding: 60px 40px 40px;
    flex: 0 0 360px;
    width: 360px;
    max-width: none;
  }

  @include mobile-480 {
    flex: 0 0 280px;
    width: 280px;
  }


  // .tariff-card__name

  &__name {
    @include adaptiv-font(36, 24);
    font-family: var(--font-family);
    font-weight: 700;
    color: var(--blue-color);
    line-height: 130%;
    margin: 0 0 30px 0;
    position: relative;
    padding-bottom: 20px;
    border-bottom: 1px solid var(--grey-light-color);

    span {
      font-family: var(--font-family);
      font-weight: 400;
    }


    &::after {
      content: "";
      position: absolute;
      bottom: -4px;
      left: 0;
      height: 8px;
      width: 100px;
      background-color: var(--accent-color);
    }
  }

  &__name[data-like="Выбор учеников"] {
    position: relative;

    &::before {
      content: "Выбор учеников";
      font-family: var(--font-family);
      font-size: 1rem;
      position: absolute;
      right: 0;
      top: -26px;
      background-image: url("../img/svg/like.svg");
      background-size: 20px;
      background-position: 0 8px;
      background-repeat: no-repeat;
      color: var(--accent-color);
      font-weight: 500;
      padding-left: 30px;
      transition: transform 0.3s;

      @include tablet-960 {
        top: -40px;
        right: 0;
        left: 0;
        width: 100%;

      }
    }

  }

  // .tariff-card__descr

  &__descr {
    color: var(--grey-dark-color);
    font-size: 1rem;
    margin-bottom: 20px;
  }

  // .tariff-card__time

  &__time {
    display: flex;
    gap: 10px;

    @include tablet-960 {
      flex-direction: column;
    }

    li {
      padding: 6px 12px;
      border-radius: 1rem;
      font-size: 14px;
      line-height: 140%;
      display: flex;
      align-items: center;
      max-width: 50%;

      @include tablet-960 {
        max-width: 100%;
      }

      &::before {
        content: "";
        width: 26px;
        height: 26px;
        flex: 0 0 26px;
        background-image: url("../img/svg/time.svg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        margin-right: 10px;
        transition: transform 0.3s;
      }
    }
  }

  // .tariff-card__number
  &__number {
    sup {
      left: 8px;
    }
  }

  // .tariff-card__time-driving

  &__time-driving {
    background-color: var(--blue-dark-color);
    color: var(--light-color);

  }

  // .tariff-card__time-theory

  &__time-theory {
    font-weight: 600;
    color: var(--blue-dark-color);
  }

  // .tariff-card__description

  &__description {
    line-height: 140%;
    margin-bottom: 30px;
  }

  // .tariff-card__price

  &__price {
    display: flex;
    justify-content: center;
    gap: 16px;

    @include tablet-960 {
      flex-direction: column;
    }

    li {
      text-align: center;
      flex: 1;
      max-width: calc(50% - 15px);
      margin-bottom: 20px;

      @include tablet-960 {
        max-width: 100%;
      }

      span {
        color: var(--grey-dark-color);
        @include adaptiv-font(18, 16);
        font-weight: 600;
        white-space: nowrap;
        display: block;
        margin-bottom: 14px;
      }
    }
  }


  // .tariff-card__footer

  &__footer {
    display: flex;
    align-items: center;
    gap: 20px;
    font-size: 16px;
    line-height: 140%;
    margin-top: 30px;
    margin-bottom: 0 !important;

    @include tablet-960 {
      flex-direction: column;
    }
  }

  // .tariff-card__installment

  &__installment {
    display: flex;
    align-items: center;

    &:before {
      content: '';
      width: 30px;
      height: 30px;
      background-image: url('../img/svg/percentage.svg');
      background-size: 30px 30px;
      background-repeat: no-repeat;
      background-position: left;
      padding-right: 40px;
    }
  }

  // .tariff-card__contribution

  &__contribution {
    span {
      font-weight: 700;
      color: var(--second-color);
      white-space: nowrap;
    }
  }
}
