// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Manrope", Arial, sans-serif;
  // --font-family: "Circe", sans-serif;
  // --font-second-family: "Helvetica", sans-serif;
  --font-second-family: "RussoOne", sans-serif;
  --content-width: 1200px;
  --content-fluid-width: 1790px;
  --container-offset: 20px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-fluid-width: calc(var(--content-fluid-width) + (var(--container-offset) * 2));
  --container-r: max(var(--container-offset), calc((100% - var(--content-width)) / 2));
  --index: calc(1vw + 1vh);
  // colors
  --light-color: #fff;
  --red-color: #80ffaa;
  --accent-color: #F4CA59;  // F4CA59
  --accent-color-09: rgba(244, 202, 89, .9);
  --accent-color-05: rgba(244, 202, 89, .5);
  --accent-color-02: rgba(244, 202, 89, .2);
  --accent-color-01: rgba(244, 202, 89, .1);
  --accent-color-heading:#e6cd88;
  --accent-color-contrast: #F4E359;
  --second-color: #0065B2;
  --second-color-lt: rgba(0, 101, 178, 0.562);
  --black-color: rgba(24, 23, 26);
  --dark-color: #242327;
  --black-color-08: rgba(24, 23, 26, .8);
  --blue-color: #112849;
  --blue-dark-color: #03152e;
  --blue-light-color: rgba(3, 21, 46, 0.36);
  --grey-color: #f8f7f5;
  --grey-light-color: #bfbfbf;
  --grey-dark-color: #8a8a9a;
  --content-color: #333333;
  --heading-color: #1d1d1b;
  --grey-color-bg: #f7f7f7;
  --grey-color-border: #dddddd;
  --box-shadow: 0 27px 35px -28px rgba(0, 0, 0, .66);
  --box-shadow-active: 0 27px 24px -37px rgba(0, 0, 0, .66);
  --box-shadow-light: 0 0.1rem 0.2rem rgba(0, 0, 0, 0.1),
    0 0.33rem 1rem rgba(0, 0, 0, 0.1);
}


