.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  color: var(--light-color);
  font-size: 12px;
  line-height: 140%;
  z-index: 110;


  &__top {
    background-color: var(--blue-light-color);
    padding: 8px 0;
  }

  &__bottom {
    background-color: transparent;
  }
}


._header._header-sticky {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  top: 0;
  background-color: var(--blue-dark-color);
  box-shadow: 0 10px 50px rgba(0, 0, 0, 0.1);
  z-index: 110;
 }

.header-nav {
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 12px;
    padding-bottom: 12px;
    gap: 20px;
    position: relative;
    z-index: 120;

    @include tablet {
      position: static;
    }
  }

  &__logo {
    flex: 0 0 190px;
  }

}

.header-info {
  // .header-info__container

  &__container {
    display: flex;
    align-items: center;
    justify-content: end;
    gap: 50px;
  }

  &--mobile {
    .header-info__container {
      flex-direction: column;
      gap: 20px;
      align-items: start;
      padding-top: 30px;
      padding-bottom: 50px;
      .header-info__social{
        flex-wrap: wrap;
      }
    }
  }

}

.header-time {
  display: flex;
  align-items: center;
  gap: 10px;
}

.header-social {
  display: flex;
  align-items: center;
  gap: 14px;
  // .header-social__icon

  &__icon {
    color: var(--accent-color);
    display: flex;
    align-items: center;
    gap: 10px;
    transition: color .3s ease-in-out;

    svg {
      width: 26px;
      height: 26px;

      path {
        fill: var(--light-color);
        transition: all .3s ease-in-out;
      }
    }

    @include hover {
      svg {
        path {
          fill: var(--accent-color);
        }
      }
    }

    span {
      transition: all .3s ease-in-out;
    }

    &--phone {
      svg {
        width: 24px;
        height: 24px;
      }
    }

    &--tel {
      font-size: 20px;
      font-weight: 700;
      white-space: nowrap;

      @include hover {
        color: var(--accent-color-09);
      }
    }
  }
}

.header-nav__menu.open {
  .header__top {
    background-color: var(--blue-dark-color);
    display: block;
  }
}

.header-info--top {

  @include small-tablet {
    .header-time {
      display: none;
    }
  }

  @include small-tablet {
    .header-time {
      display: none;
    }
  }

  @include mobile-min {
    .header-social__icon--mail {
      display: none;
    }
  }
}
