.nav {

  &__list {
    display: flex;
    align-items: center;
    margin: 0;
  }

  &__list--dropdown {
    position: absolute;
    background-color: var(--light-color);
    right: 50%;
    top: 100%;
    transform: translateX(50%);
    padding: 30px;
    display: block;
    width: 240px;
    box-shadow: var(--box-shadow);
    transition: all 0.3s;
    opacity: 0;
    visibility: hidden;

    h2,
    a {
      letter-spacing: normal;
      line-height: 120%;
    }

    a {
      font-size: 16px;
      font-weight: 500;
      color: var(--dark-color);
      transition: color .3s ease-in-out;

      @include hover {
        color: var(--accent-color);
      }
    }

    h2 {
      font-size: 18px;
      position: relative;
      margin-bottom: 40px;
      margin-top: 20px;

      &::before {
        content: '';
        position: absolute;
        bottom: -10px;
        left: 0;
        height: 1px;
        width: 150px;
        background-color: var(--grey-light-color);
      }

      &::after {
        content: '';
        position: absolute;
        bottom: -12px;
        left: 0;
        height: 5px;
        width: 50px;
        background-color: var(--accent-color);
      }
    }
  }

  &__item {
    position: relative;
    padding: 10px 0;

    &:not(:first-child) {
      margin-left: 24px;
    }

    &--mega {
      position: static;
    }
  }

  &__link {
    color: var(--light-color);
    font-size: 16px;
  }

  &__link--drop::after {
    content: "";
    width: 12px;
    height: 8px;
    background-image: url("../img/svg/arrow.svg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: inline-block;
    margin-left: 8px;
    transition: transform 0.3s;
  }
}



.dropdown-submenu {
  display: flex;
  justify-content: space-between;
  align-items: initial;
  flex-wrap: wrap;
  right: 0;
  width: 100%;
  transform: translateX(0);
  transform: translateY(-20px);

  &__item {
    flex: 0 0 33.333%;
    min-width: 200px;
    padding: 0 30px;

    &:first-child {
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;
    }
  }

  &__list {
    padding: 0;
  }
}



.mobile-back {
  display: none;
}

.dropdown-list__item {
  margin-bottom: 10px;
}

.dropdown-submenu__list-item {
  margin-bottom: 10px;
}

@include tablet-m {
  .nav__link--drop:hover::after {
    transform: rotate(180deg);
    transition: all 0.3s;
  }

  .nav__item:hover .nav__list--dropdown {
    visibility: visible;
    opacity: 1;
    transition: all 0.3s;
  }
}

.burger {
  flex: 0 0 44px;
  width: 44px;
  height: 44px;
  cursor: pointer;
  background-color: transparent;
  border: none;
  position: relative;
  display: none;

  &__line {
    position: absolute;
    left: 50%;
    top: 50%;
    background-color: var(--light-color);
    height: 3px;
    width: 34px;
    border-radius: 20px;
    transform: translateX(-50%) translateY(-50%);

    &:first-child {
      margin-top: -10px;
    }

    &:last-child {
      left: 5px;
      margin-top: 8px;
      width: 24px;
      transform: translateY(50%);
    }
  }
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 99;
  opacity: 0;
  visibility: hidden;
}

.lock {
  overflow: hidden;
}

@include tablet {
  .burger {
    display: block;
  }

  .mobile-back {
    display: block;

    a {
      position: relative;
      font-weight: 700;
      border: none;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      background-color: var(--accent-color);

      @include hover {
        color: var(--blue-color);
      }

      &::before {
        content: "";
        width: 10px;
        height: 10px;
        background-image: url("../img/svg/arrow-b.svg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        display: inline-block;
        margin-right: 10px;
        transform: rotate(90deg);
      }
    }
  }

  .nav {
    position: fixed;
    left: 0;
    top: 0;
    max-width: 400px;
    width: 80%;
    height: 100vh;
    z-index: 100;
    box-shadow: 5px 0 15px -5px rgba(0, 0, 0, 0.8);
    background-color: #fff;
    transform: translateX(-150vw);
    overflow-x: hidden;
    overflow-y: auto;


    &__list {
      flex-direction: column;
      align-items: flex-start;
      padding: 0;
      padding-top: 60px;
      position: relative;

      &--dropdown {
        padding-top: 0;
      }
    }

    &__item {
      margin-left: 0 !important;
      font-size: 20px;
      width: 100%;
      padding: 0;
      position: static;
    }

    &__link::after {
      transform: rotate(-90deg);
    }

    &__list--dropdown {
      position: absolute;
      top: 0;
      right: auto;
      left: 100%;
      box-shadow: none;
      width: 100%;
      display: none;
    }

    &__link--drop::after {
      background-image: url("../img/svg/arrow-b.svg");
    }

    &__list.transformation {
      transform: translateX(-100%);
      transition: all 0.3s;
    }

    &__list--dropdown.transformation {
      opacity: 1;
      visibility: visible;
      display: block;
      transform: none;
      background-color: #fff;
      transition: all 0.3s;
    }

    &.open {
      transform: translateX(0);
      transition: all 0.3s;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    &__list--dropdown {
      h2 {
        margin-bottom: 30px;
        margin-top: 50px;

        &::before,
        &::after {
          left: 30px;
        }
      }
    }
  }


  .nav__link,
  .dropdown-submenu__list-item a,
  .dropdown-list__link {
    padding: 20px 30px;
    font-size: 16px;
    font-weight: 500;
    color: var(--blue-color);
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--grey-light-color);

    @include hover {
      color: var(--accent-color);
    }
  }



  .dropdown-submenu {
    &__item {
      flex: 0 0 100%;
      width: 100%;
      padding: 0;
      border: none !important;
    }

    &__title {
      padding: 0 30px;
    }

    &__banner {
      display: inline-block;
      padding: 20px;
    }
  }

  .overlay.open {
    opacity: 1;
    visibility: visible;
    transition: all 0.3s;
  }
}

