.form-section {
  width: 100%;
}

.form {
  &__input-box {
    display: block;
    width: 100%;
    margin-bottom: 14px;
  }

  &__button {
    margin-top: 30px;
  }

  &__input {
    width: 100%;
    background-color: var(--light-color);
    outline: none;
    border-radius: 6px;
    border: 1px solid var(--grey-color-border);
    padding: 20px;
    font-weight: 600;
    color: var(--content-color) !important;
    font-size: 18px;

    &::placeholder {
      font-weight: 400;
      font-size: 18px;

      @include mobile {
        font-size: 14px;
      }
    }

    &:focus,
    &:active {
      background-color: var(--light-color);
      border: 1px solid var(--accent-color);
    }

  }

  &__label {
    color: var(--grey-dark-color);
    padding: 7px 20px;
    font-size: 14px;
  }

  &__pers {
    font-size: 14px;
    line-height: 120%;
    padding: 20px;

    a {
      color: var(--second-color);
    }
  }
}