._p40 {
  padding-top: 40px;
  padding-bottom: 40px;
}

._p60 {
  padding-top: 60px;
  padding-bottom: 60px;
}

._p80 {
  padding-top: 80px;
  padding-bottom: 80px;
}

._p100 {
  padding-top: 100px;
  padding-bottom: 100px;
}

._mb40 {
  margin-bottom: 40px;
}

._mb60 {
  margin-bottom: 60px;
}

._m0 {
  margin: 0;
}

.breadcrumbs {
  line-height: 1.4;
  list-style: none;
  margin: 0 0 40px 0;
  padding: 0;
  font-size: 15px;

  li {
    display: inline-block;
    vertical-align: top;
    margin: 0;
    font-weight: 500;
    color: var(--content-color);

    a {
      color: var(--second-color);
    }
  }

  li+li {
    margin: 0 0 0 1px;

    &::before {
      content: '';
      margin-right: 6px;
      width: 14px;
      height: 14px;
      display: inline-block;
      transform: translateY(2px);
      background: url('../img/svg/next.svg') 0 0 / cover no-repeat;
    }
  }
}


.section-bg {
  display: flex;
  justify-content: center;
  align-items: center;

  &__container {
    position: relative;
    z-index: 5;
    opacity: 1;
    display: flex;
    align-items: center;
    gap: 100px;

    @include tablet-960 {
      flex-direction: column;
      text-align: center;
      gap: 30px;
    }

  }

  &__content {
    width: 100%;
    color: var(--light-color);
    flex: 1;


    p {
      line-height: 170%;
    }

  }

  &__btn {
    flex: 0 0 300px;
    text-align: center;

    @include tablet-960 {
      flex: 1;
    }
  }

  &--img {
    position: relative;
    height: 500px;
    width: 100%;
    margin: 0 auto;
    background-color: var(--black-color);
    overflow: hidden;

    @include mobile {
      height: 600px;
    }

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      display: block;
      height: 500px;
      width: 100%;
      opacity: 0.3;
      background: url('../img/bg/bg2.webp') no-repeat;
      background-size: cover;
      background-position: 0;
      transition: all 1s ease;
      transform: scale(1.1);

      @include mobile {
        height: 600px;
      }
    }

    @include hover {
      &::before {
        background-position: -50px;
        opacity: .5;
      }
    }
  }

  &--pattern {}
}

.underline {
  text-decoration: underline;
  text-underline-offset: 0.2em;
  text-decoration-skip-ink: none;
  text-decoration-color: var(--accent-color);
  text-decoration-thickness: 0.15em;
  font-weight: 600;
}

.number {
  font-size: 52px;
  color: var(--accent-color);
  font-family: var(--font-second-family);

  sup {
    font-size: 52%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
    left: 0;
    top: -0.7em;

    &::before {
      content: 'RUB';
      position: absolute;
      left: 2px;
      top: 37px;
      font-size: 71%;
      letter-spacing: 0.3em;
    }
  }
}

.shadow {
  border-color: transparent;
  box-shadow: 0 27px 24px -37px rgba(0, 0, 0, 0.66);
  transition: all 0.3s ease-in-out;

  @include hover {
    box-shadow: 0 27px 35px -28px rgba(0, 0, 0, 0.66);
  }
}

[class$="--center"] {
  text-align: center;
}

[data-flex="4"] {
  display: flex;
  justify-content: space-between;

  &>* {
    flex: 0 1 23%;
    padding-top: 20px;
    padding-bottom: 20px;
  }

  @include mobile-max {
    flex-wrap: wrap;

    &>* {
      flex: 0 1 45%;
      max-width: 45%;
    }

  }

  @include mobile-480 {
    flex-direction: column;


    &>* {
      flex: 1;
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
}

[data-flex="3"] {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;

  &>* {
    flex: 0 0 30%;
    width: 30%;
  }

  @include tablet-830 {
    &>* {
      flex: 0 0 45%;
      width: 45%;
    }
  }

  @include mobile {
    &>* {
      flex: 0 0 100%;
      width: 100%;
    }
  }
}

[data-flex="2"] {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;

  &>* {
    flex: 0 0 calc(50% - 10px);
    max-width: calc(50% - 10px);
  }

  @include mobile {
    flex-direction: column;


    &>* {
      flex: 1;
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
}

button {
  user-select: none;
  cursor: pointer;
  display: inline-block;
  margin: 0;
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: 120%;
  font-weight: 600;
  background-color: transparent;
  box-sizing: border-box;
  outline: none;
  white-space: nowrap;
  text-decoration: none;
  transition: all .3s ease-in-out;
  border: none;
}

.desc {
  color: var(--grey-dark-color);
  &--center {
    max-width: 900px;
    margin: 0 auto;

  }
}
