.steps-section {

  // .steps-section__title

  &__title {
    // width: 600px;
    max-width: 600px;
    margin: 0 auto;
    margin-bottom: 24px;
  }

  // .steps-section__desc

  &__desc {
    position: relative;
    margin-bottom: 40px;

    @include tablet-1120 {
      margin-bottom: 195px;
    }

    &::after {
      position: absolute;
      content: '';
      top: 0;
      right: -250px;
      width: 250px;
      height: 189px;
      background: url('../img/decor/prava250.webp') 0 0 / contain no-repeat;

      @include tablet-1120 {
        top: 100%;
        right: 50%;
        transform: translateX(50%);

      }
    }
  }

  // .steps-section__items

  &__items {
    margin-top: 60px;

    @include tablet-1120 {
      margin-top: 184px;
    }
  }
}



.steps {
  gap: 30px;


  // .steps__item

  &__item {}

  // .steps__num

  &__num {
    color: var(--grey-dark-color);
    font-size: 60px;
    font-family: var(--font-second-family);
    letter-spacing: 2px;
    margin-bottom: 50px;
  }

  // .steps__num--y

  &__num--y {
    color: var(--accent-color);
  }

  // .steps__title

  &__title {
    @include adaptiv-font (26, 24);
    font-weight: 800;
    color: var(--blue-color);
    padding-bottom: 10px;
    position: relative;
    margin-bottom: 40px;


    &::before,
    &::after {
      content: '';
      position: absolute;
      left: 0;
    }

    &::before {
      bottom: -10px;
      height: 1px;
      width: 100%;
      background-color: var(--grey-light-color);
    }

    &::after {
      bottom: -17px;
      height: 14px;
      width: 74px;
      background: url('../img/svg/arrow-right.svg') 0 0 / contain no-repeat;
    }

  }

  // .steps__desc

  &__desc {
    color: var(--grey-dark-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.05em;

  }
}