.certificate {

  // .certificate__container

  &__container {}

  // .certificate__content

  &__content {
    background-color: var(--grey-color-bg);
    border-radius: 44px;
    padding: 60px 150px;

    @include tablet-830 {
      padding: 40px;
    }

    @include mobile {
      padding: 20px;
    }
  }

  // .certificate__title

  &__title {
    max-width: 500px;
    text-align: center;
    margin: 0 auto 50px;

    h2,
    h1 {
      text-align: center;
      text-transform: uppercase;
      line-height: 110%;

      span {
        color: var(--accent-color);
        letter-spacing: 4.4px;
      }

      @include mobile {
        font-size: 26px !important;

        span {
          letter-spacing: 2.4px;
        }
      }
    }


  }

  // .certificate__image

  &__image {
    margin-bottom: 30px;
  }

  // .certificate__form

  &__form {
    max-width: 600px;
    margin: 0 auto;
  }
}

