.blocks {

  // .blocks__title

  &__title {}

  // .blocks__items

  &__items {
    justify-content: space-between;

    @include tablet-960 {
      gap: 40px;
      flex-wrap: nowrap;
      overflow-x: auto;
    }
  }

  // .blocks__item

  &__item {
    // background-color: var(--blue-color);
    // color: var(--light-color);
    padding: 20px 0;
    border-radius: 24px;
    @include tablet-960 {
      width: 270px;
      flex:0 0 270px;
    }
    p{
      font-size: 16px;
      line-height: 160%;
    }
    h2,h3{
      font-size: 24px;
      // color: var(--light-color);
      line-height: 120%;
      text-align: center;
    }
  }

  // .blocks__img

  &__img {
    text-align: center;
  }
}
