/* Стили для модального окна */
.modal {
  display: none;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);

  &-text{
    margin-bottom: 30px;
  }
}

.modal-content {
  margin: 10% auto;
  width: 80%;
  max-width: 600px;
  background-color: var(--grey-color-bg);
  border-radius: 1rem;
  box-shadow: var(--box-shadow-light);
  transition-duration: 0.3s;
  padding: 40px;
  position: relative;

  @include mobile {
    margin: 0;
    width: 100%;
    height: 100vh;
    border-radius: 0;
    overflow-y: auto;
  }


  &--auto {
    background: var(--grey-color-bg) url('../img/decor/auto-topaz.webp') -140px center / 270px no-repeat;
    padding-left: 140px;

    @include mobile {
      background: var(--grey-color-bg) url('../img/decor/auto-topaz.webp') -140px 50px / 270px no-repeat;
    }

    @include mobile-min {
      background: var(--grey-color-bg);
      padding-left: 40px;
    }
  }

  &__top {
    margin-bottom: 30px;
  }

  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 30px;
    height: 30px;
    margin: 0;
    padding: 0;
    background: url('../img/svg/close.svg') 0 0 / cover no-repeat;
    border: none;
    transition: transform .3s ease-in-out;

    @include hover {
      transform: scale(1.1);
    }
  }
}


/* Стили для радио кнопок и чекбокса */
input[type="radio"],
input[type="checkbox"] {
  margin-right: 5px;
}

.modal-open {
  overflow: hidden;
}
