.tabs__item {
  display: none;
}

.tabs__item.active {
  display: block;
}


.tabs__nav {
  display: flex;
  overflow-x: auto;


  &-btn {
    position: relative;
    color: var(--blue-dark-color);
    display: flex;
    gap: 10px;
    align-items: center;
    transition: all ease .3s;
    padding: 1em 1.2em;
    margin: 20px 0;
    // transform: translate3d(0, 0, 0);
    opacity: .7;
    white-space: nowrap;
    cursor: pointer;
    font-size: 24px;
    font-weight: 800;
    border: 0;
    background: transparent;

    @include hover {
      opacity: 1;
    }

    &:after {
      transition: all .3s cubic-bezier(1, 0, 0, 1);
      will-change: transform, box-shadow, opacity;
      position: absolute;
      content: '';
      height: 3px;
      bottom: 0px;
      left: 0px;
      right: 0px;
      border-radius: 3px 3px 0px 0px;
      // background: var(--second-color-lt);
      background: var(--accent-color);
      box-shadow: 0px 4px 10px 3px rgba(var(--accent-color), .15);
      opacity: 0;
      transform: scale(0, 1);
    }

    &.active {
      opacity: 1;

      &:after {
        opacity: 1;
        transform: scale(1, 1);
      }
    }

    &--auto,
    &--moto,
    &--auto-moto,
    &--ind {
      &::before {
        content: '';
        width: 50px;
        height: 30px;
        top: 0;
        left: 0;
      }
    }

    &--auto::before {
      background: url('../img/svg/auto.svg') 0 0 / cover no-repeat;
    }

    &--moto::before {
      background: url('../img/svg/moto.svg') 0 0 / cover no-repeat;
    }

    &--auto-moto::before {
      background: url('../img/svg/moto-auto.svg') 0 0 / cover no-repeat;
    }

    &--ind::before {
      background: url('../img/svg/moto-auto-ind.svg') 0 0 / cover no-repeat;
    }
  }
}
