.swiper-wrapper {
  align-items: stretch
}

.swiper-slide {
  height: auto;
}

.swiper-hero {
  width: 100%;
  height: 100%;
  position: relative;

  // .swiper-hero__content
  &__content {
    z-index: 2;
    text-align: left;
    flex: 0 0 100%;
    box-sizing: border-box;
  }

  // .swiper-hero__text
  &__text {
    margin-bottom: 60px;
    max-width: 720px;


    li {
      margin-bottom: 14px;
      position: relative;
      padding-left: 24px;

      &::before {
        content: '';
        position: absolute;
        background: url('../img/svg/list.svg') 0 0 / cover no-repeat;
        left: 0;
        top: 4px;
        width: 11px;
        height: 17px;
      }
    }
  }

  // .swiper-hero__slide
  &__slide {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    background-color: var(--black-color);
    color: var(--light-color);
    position: relative;

    img {
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      z-index: 1;
      filter: brightness(40%);
    }
  }

  // .swiper-hero__btns, .swiper-hero__desc
  &__btns,
  &__desc {
    display: flex;
    align-items: center;
  }

  &__desc {
    margin-left: 30px;

    a {
      text-decoration: underline;
      color: var(--accent-color);
      margin: 0 14px;
      transition: color .3s ease-in-out;

      @include hover {
        color: var(--light-color);
      }
    }
  }

  &__content {
    padding-top: 150px;
    padding-bottom: 100px;
  }
}

.pagination-hero {
  position: absolute;
  width: 20px;
  height: 500px;
  right: 50px;
  top: 120px;
  z-index: 15;

  // .pagination-hero__nav

  &__nav {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: 20px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    gap: 10px;

    .swiper-pagination-bullet {
      background-color: var(--grey-dark-color);
      width: 8px;
      height: 40px;
      border-radius: 1px;

      &-active {
        background-color: var(--accent-color);
      }
    }
  }
}


@include mobile-max {
  .swiper-hero__btns {
    flex-direction: column-reverse;
    align-items: start;

    &--link {
      flex-direction: row;
      flex-wrap: wrap;

      .swiper-hero__desc {
        margin-left: 0px;
        margin-bottom: 30px;
        margin-top: 20px;
        flex-direction: row;
      }
    }
  }

  .swiper-hero__desc {
    margin-left: 0;
    margin-bottom: 30px;
  }
}

@include mobile {
  .swiper-hero__content {
    padding-top: 130px;
    padding-bottom: 70px;
    padding-right: 64px;
  }

  .pagination-hero {
    right: 18px;
  }

  .swiper-hero__desc {
    flex-direction: column;
    align-items: start;
  }

  .price-hero__num {
    margin-bottom: 20px;
  }

}