.online-section {
  background-color: var(--accent-color);
  display: flex;
  position: relative;

  &__container {
    flex: 0 0 55%;
    width: 55%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 40px;
    padding-bottom: 40px;
    position: relative;
    overflow: visible;
    z-index: 3;

    h2 span {
      display: inline-block;
      color: var(--light-color);
      @include adaptiv-font (64, 34);
    }

    ul {
      display: flex;
      gap: 20px;
      margin-bottom: 40px;

      li {
        background-color: rgba(255, 255, 255, 0.3);
        border-radius: 8px;
        font-size: 16px;
        text-align: center;
        font-weight: 500;
        line-height: 130%;
        padding: 20px 14px;
        display: flex;
        align-items: center;
        color: var(--blue-color);
        transition: all .3s ease-in-out;

        @include hover {
          transform: scale(1.1);
        }
      }
    }
  }
}

.poster {
  flex: 0 0 45%;
  width: 45%;
  position: relative;
  overflow: hidden;
  border: 0;
  width: 100%;
  z-index: 0;

  img {
    height: 100%;
  }

  .triangle {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 100%;
  }

  .triangle path {
    fill: var(--accent-color);
  }
}
