/* stylelint-disable */

@mixin for-desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

// @include tablet-m{}
@mixin tablet-m {
  @media (min-width: (1100px)) {
    @content;
  }
}
// @include tablet-1120{}
@mixin tablet-1120 {
  @media (max-width: (1120px)) {
    @content;
  }
}

// @include tablet{}
@mixin tablet {
  @media (max-width: (1099.98px)) {
    @content;
  }
}
// @include tablet-960{}
@mixin tablet-960 {
  @media (max-width: (960px)) {
    @content;
  }
}
// @include tablet-830{}
@mixin tablet-830 {
  @media (max-width: (830px)) {
    @content;
  }
}
// @include small-tablet{}
@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

// @include mobile-max{}
@mixin mobile-max {
  @media (max-width: (670px)) {
    @content;
  }
}
// @include mobile{}
@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
// @include mobile-480{}
@mixin mobile-480 {
  @media (max-width: (480px)) {
    @content;
  }
}
// @include mobile-min{}
@mixin mobile-min {
  @media (max-width: (380px)) {
    @content;
  }
}

